(function() {
  'use strict';

  angular
    .module('app')
    .service('scroll', scroll);

  /** @ngInject */
  function scroll() {
    var _self = this;

    _self.getPosition = function (elementId) {
      var e = document.getElementById(elementId);
      var y = e.offsetTop;
      var node = e;

      while (node.offsetParent && node.offsetParent != document.body) {
        node = node.offsetParent;
        y += node.offsetTop;
      }

      return y;
    };

    _self.getCurrentPosition = function () {
      // Firefox, Chrome, Opera, Safari
      if (self.pageYOffset) {
        return self.pageYOffset;
      }
      // Internet Explorer 6 - standards mode
      if (document.documentElement && document.documentElement.scrollTop) {
        return document.documentElement.scrollTop;
      }
      // Internet Explorer 6, 7 and 8
      if (document.body.scrollTop) {
        return document.body.scrollTop;
      }

      return 0;
    }

    _self.scrollToHash = function(elementId, seconds) {
      _self.scrollTo(_self.getPosition(elementId), seconds);
    }

    _self.scrollTo = function (position, seconds) {
      var seconds = seconds <= 0 ? 1000 : seconds * 1000;

      var start = _self.getCurrentPosition();
      var stop = position;
      var distance = stop > start ? stop - start : start - stop;
      var frames = 25;
      var frameSeconds = 0;
      var step = distance / frames;

      for (var i = 1; i <= frames; i++) {
        var nextPosition = stop > start ? (start + i * step) : (start - i * step);

        window.setTimeout('window.scrollTo(0, ' + nextPosition + ')', frameSeconds);
        frameSeconds += seconds / frames;
      }
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .directive('menuScroll', menuScroll);

  /** @ngInject */
  function menuScroll($window) {
    return {
      restrict: 'EA',
      scope: {
        items: '=list'
      },
      controller: 'menuScrollController',
      controllerAs: 'controller',
      templateUrl: 'app/packages/menuScroll/menuScroll.html',
      link: function ($scope, element, attrs) {
        var $page = angular.element($window);
        var menu = angular.element(document.getElementById('menuScroll-element'));
        var offsetLimit = menu[0].offsetTop - menu[0].offsetHeight;

        function menuTrigger () {

          if ($window.innerWidth >= 1170) {
            if ($window.pageYOffset >= offsetLimit) {
              $scope.menuScrollMenuFixed = true;
              menu.addClass('is-fixed');
              setTimeout(function() {
                menu.addClass('animate-children');
              }, 50);
            } else {
              $scope.menuScrollMenuFixed = false;
              menu.removeClass('is-fixed');
              setTimeout(function() {
                menu.removeClass('animate-children');
              }, 50);
            }
          }

          angular.forEach(document.querySelectorAll('.cd-section'), function(value) {
             var actual = angular.element(value);

              angular.forEach(document.querySelectorAll(".cd-anchor"), function(value) {
                var actualAnchor = angular.element(value);

                if (actualAnchor.attr('hash') === actual.attr('id')) {
                  if (
                    ( actual[0].offsetTop - menu[0].offsetHeight <= $window.pageYOffset )
                    && ( actual[0].offsetTop +  actual[0].offsetHeight - menu[0].offsetHeight > $window.pageYOffset )
                  ) {
                    actualAnchor.addClass('cd-active');
                  } else {
                    actualAnchor.removeClass('cd-active');
                  }
                }
              });
          });

          $scope.$apply();
        }

        $page.bind('scroll', menuTrigger);
      }
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .controller('menuScrollController', menuScrollController);

  /** @ngInject */
  function menuScrollController($window, $location, $scope, scroll) {
    var _self = this;

    $scope.menuScrollMenuFixed = false;

    _self.clicked = function (state, hash) {
      if (state !== undefined) {
        $location.path(state);
      } else {
        scroll.scrollTo(scroll.getPosition(hash) - document.getElementById('menuScroll-element').offsetHeight, 0.2);
        angular.element(document.querySelector('.cd-secondary-nav-trigger')).removeClass('menu-is-open');
        angular.forEach(document.getElementById('menuScroll-element').querySelectorAll('ul'), function (value) {
          angular.element(value).removeClass('is-visible');
        });
      }
    };

    _self.menu = function() {
      angular.element(document.querySelector('.cd-secondary-nav-trigger')).toggleClass('menu-is-open');
      angular.forEach(document.getElementById('menuScroll-element').querySelectorAll('ul'), function (value) {
        angular.element(value).toggleClass('is-visible');
      });
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .service('PostService', PostService);

  /** @ngInject */
  function PostService($q, $http, $showdown) {
    var _self = this;

    _self.list = function (page) {
      function getPage(page) {
        return $http.get('assets/posts/pagination/' + page + '.json');
      }

      function getPost(id, filename) {
        return $http.get('assets/posts/post/' + filename).then(function (response) {
          return {
            id: id,
            filename: filename,
            content: response.data
          };
        });
      }

      return getPage(page).then(function (response) {
        var promises = [], item;

        for (var i = 0, c = response.data.length; i < c; i++) {
          item = response.data[i];

          promises.push(
            getPost(item.id, item.filename).then(
              function (response) {
                return {
                  page: page,
                  id: response.id,
                  createdAt: new Date(response.filename.substring(0, 25)),
                  content: $showdown.makeHtml(response.content)
                };
              }
            )
          );
        }

        return $q.all(promises);
      });
    };

    _self.get = function (page, id) {
      var defarred = $q.defer();

      function getPage(page) {
        return $http.get('assets/posts/pagination/' + page + '.json').success(function(response) {
          return response;
        });
      }

      function getPost(filename) {
        return $http.get('assets/posts/post/' + filename).success(function(response) {
          return response;
        });
      }

      getPage(page).then(function (response) {
        angular.forEach(response.data, function(item) {
          if (item.id == id) {
            getPost(item.filename).then(function (response) {
              defarred.resolve({
                page: page,
                id: item.id,
                createdAt: new Date(item.filename.substring(0, 25)),
                content:$showdown.makeHtml(response.data)
              });
            });
          }
        });
      });

      return defarred.promise;
    };
  }

})();

